.QRCode{
  width: 100%;
  height: 100vh;
  position: relative;
  background: rgb(116, 116, 219);
  display: flex;
  justify-content: center;
  align-items: flex-end;
  overflow: hidden;
  flex-wrap: wrap;
  flex-direction: row;
  button{
    width: auto;
    font-family: 'Poppins',sans-serif;
    font-weight: 700;
    font-size: 1rem;
    border: 0px;
    padding: 1rem;
    background: rgb(7,92,212);
    background: rgb(255,0,127);
    background: -moz-linear-gradient(90deg, rgba(255,0,127,1) 7%, rgba(250,1,217,1) 86%);
    background: -webkit-linear-gradient(90deg, rgba(255,0,127,1) 7%, rgba(250,1,217,1) 86%);
    background: linear-gradient(90deg, rgba(255,0,127,1) 7%, rgba(250,1,217,1) 86%);
    filter: progid:DXImageTransform.Microsoft.gradient(startColorstr="#ff007f",endColorstr="#fa01d9",GradientType=1);
    border-radius: 10px;
    display: flex;
    margin: 0px auto;
    cursor: pointer;
    transition: all ease-in-out 0.3s;
    color: #FFF;
    transform: translateY(0px), scale(1.1);
    &:hover{
      transform: translateY(0px), scale(1.1);
      background: rgb(250,1,217);
background: -moz-linear-gradient(90deg, rgba(250,1,217,1) 7%, rgba(255,0,127,1) 86%);
background: -webkit-linear-gradient(90deg, rgba(250,1,217,1) 7%, rgba(255,0,127,1) 86%);
background: linear-gradient(90deg, rgba(250,1,217,1) 7%, rgba(255,0,127,1) 86%);
filter: progid:DXImageTransform.Microsoft.gradient(startColorstr="#fa01d9",endColorstr="#ff007f",GradientType=1);
    }
  }
  &__Model{
    &__Block{
      width: 300px;
      padding: 3rem;
      display: flex;
      border-top-left-radius: 2rem;
      border-top-right-radius: 2rem;
      flex-wrap: wrap;
      h3{
        font-family: 'Poppins',sans-serif;
        font-weight: 700;
        font-size: 1.5rem;
        color: #FFF;
        text-align: center;
        width: 100%;
        margin: 20px;
      }
      h5{
        font-family: 'Poppins',sans-serif;
        font-weight: 700;
        font-size: 1.1rem;
        color: #FFF;
        text-align: center;
        width: 100%;
        margin: 0px;
      }
      img{
        width: 100%;
        border-radius: 2rem;
      }
      background: blue;
      padding: 2rem 2rem 5rem;
    }
  }
}
#QRCode__Model{
  &.ativaQR{
    .QRCode__Model__Block{
      transform: translateY(0%);
    }
    button{
      transition: all ease-in-out 0.3s;
      transform: translateY(-100%);
      opacity: 0;
      visibility: hidden;
      overflow: hidden;
      
    }
  }
  .QRCode__Model__Block{
    transform: translateY(100%);
    transition: all ease-in-out 0.3s;
   
  }
}